import humanReadableDaysAgo from '../util/humanReadableTimeAgo'
import { EditorState, convertFromRaw } from 'draft-js'
import Draggable from 'react-draggable'
import editThread from '../util/fetchApiHelpers/editThread'

export default class JamPreview extends React.Component {
    constructor(props) {
        super(props)
        const { thread } = props
        this.avatars = this.createAvatarsArray()
        this.previewText = this.createMessagePreview()
    }

    createMessagePreview = () => {
        const { thread } = this.props
        const jsonifiedCommentMessage = JSON.parse(thread.comments[thread.comments.length - 1].message)
        const messageConvertedFromRaw = convertFromRaw(jsonifiedCommentMessage)    
        const tempEditorState = EditorState.createWithContent(messageConvertedFromRaw)
        let commentText = tempEditorState.getCurrentContent().getPlainText('\u0001')
        return commentText.replace('\u0001', ' ')
    }

    createAvatarsArray = () => {
        const { thread } = this.props
        let authorsArray = []
        thread.comments.forEach((comment) => {
            // if not alreadt in the authors aray, add it
            if (!authorsArray.some(author => author.name === comment.author.name) && authorsArray.length < 6) {
                authorsArray.push(comment.author)
            } 
        })

        return authorsArray.map((author) => <img key={author._id} src={author.avatar} />)
    }

    handleDragStart = () => {
        this.props.toggleDragStatus(true)
    }

    handleDragEnd = async (e) => {
        const { thread, updateThread, toggleDragStatus, handlePreviewClick, updateThreadPosition } = this.props
        console.log(e);
        
        toggleDragStatus(false)
        if (!e.target.className.includes('preview-dragger') && e.type !== "mouseup") {
            console.log('not a real click, opening the preview');
            
            // not a real drag just open the preview    
            handlePreviewClick(e)
        } else {
            const { x, y } = this.getMousePosWithinElement(e)
            updateThreadPosition(x, y)
            const updatedThread = await editThread(x, y, thread._id)
            await updateThread(updatedThread, false)
        }
    }

    handleOnDrag = (e) => {
        const { updateThreadPosition } = this.props

        if (e.type === 'mousemove') { // e.target.className.includes('preview-dragger')            
            // updateThreadPosition(e.pageX, e.pageY)
        }
    }

    stopImageFromDragging = (e) => {
        e.preventDefault()
    }

    getMousePosWithinElement = (e) => { 
        console.log(e.target.parentNode)       
        const element = e.target.parentNode.getBoundingClientRect();
        console.log(element);
        
        const x = e.clientX - (e.clientX - element.left); 
        const y = e.clientY - (e.clientY - element.top);  

        return { x, y }
    }

    render() {
        const { thread, handlePreviewClick } = this.props
        const { threadX, threadY } = this.props
        return (
            // <Draggable handle=".preview-dragger" onStart={this.handleDragStart} onStop={this.handleDragEnd} onDrag={this.handleOnDrag}>
            <div onClick={handlePreviewClick} key={`jam-preview-${thread._id}`} style={{ top: `${threadY ? threadY : thread.y}px`, left: `${threadX ? threadX : thread.x}px` }} id={`preview-thread-${thread._id}`} className={`jam-preview ${thread._id}`}>
                <div className="preview-container">
                    {/* <img onDragStart={this.stopImageFromDragging} className="preview-dragger" src="/img/icons/draggable.svg" /> */}
                    <div className="preview-avatars">{this.avatars}</div>
                    <div className="preview-num-comments">{`${thread.comments.length} ${thread.comments.length > 1 ? "replies" : "comment"}`}</div>
                    <p className="preview-text">{this.previewText}</p>
                    <div className="preview-reply-date">{humanReadableDaysAgo(thread.comments[thread.comments.length - 1]._id)}</div>
                </div>
            </div>
            // </Draggable>
        )
    }
}