// used to reposition a thread

import * as config from '../config'
import getJamFilledCookie from '../getJamFilledCookie'

export default async (x,y, threadId) => {
    try {
        const res = await fetch(`${config.API_HOSTNAME}/api/plugin/jam/${config.JAM_ID}/threads/${threadId}/edit`, {
            method: 'POST',
            headers: { 
                'Content-Type': 'application/json', 
                'jam-filled-cookie': getJamFilledCookie()
            },
            body: JSON.stringify({
                x, 
                y
            })
        })

        const json = await res.json()     
        
        // returns updated thread object
        return json
    } catch (e) {
        return false
    }   
}