import JamPreview from './JamPreview'
import humanReadableTimeFrom from '../util/humanReadableTimeAgo'
import resolveThreadById from '../util/fetchApiHelpers/resolveThreadById'
import JamThreadEditor from './JamThreadEditor'
import JamEditorDisplayMode from './JamEditorDisplayMode'
import Draggable from 'react-draggable'
import editThread from '../util/fetchApiHelpers/editThread'

export default class JamThread extends React.Component {
    constructor(props) {
        super(props)
        const { thread } = props
        console.log('in the jamthread constructor')
        this.setState({
            threadX: thread.x,
            threadY: thread.y
        })
    }

    state = { threadOpen: false, dragging: false }
 
    handlePreviewClick = (e) => {
        const { threadOpen, threadIndex, updateWhichThreadIsOpen } = this.props
        
        // when one thread opens, other threads must close
        if (!e.target.className.includes('preview-dragger')) updateWhichThreadIsOpen(!threadOpen ? threadIndex : -1)     
    }

    handleThreadClick = (e) => {
        const { changeState } = this.props
    }

    handleResolve = async (e) => {
        const { thread, deleteThread } = this.props
        await resolveThreadById(thread._id)
        deleteThread(thread._id)
    }

    handleDragStart = () => {
        this.setState({ dragging: true })
    }

    handleDragEnd = async (e) => {
        const { thread, updateThread } = this.props
        this.setState({ threadX: e.pageX, threadY: e.pageY, dragging: false })
        const updatedThread = await editThread(e.pageX, e.pageY, thread._id)
        await updateThread(updatedThread)
    }

    handleOnDrag = async (e) => {
        this.setState({ threadX: e.pageX, threadY: e.pageY })
    }

    toggleDragStatus = (status) => {
        const { dragging } = this.state
        this.setState({ dragging: status })
    }

    updateThreadPosition = (x,y) => {
        this.setState({ threadX: x, threadY: y })
    }

    render() {
        const { thread, threadOpen, updateThread, loggedInUser } = this.props
        const { threadX, threadY } = this.state
        if (threadOpen) {
            return (
                // <Draggable handle=".thread-header-container" onStop={this.handleDragEnd} onDrag={this.handleOnDrag} >
                <div style={{ top: `${threadY ? threadY : thread.y}px`, left: `${threadX ? threadX : thread.x}px`, zIndex: `${threadOpen ? 1000001 : 1000000 }` }} id={`thread-container-${thread._id}`} className={`thread-container ${thread._id}`}>
                    <div className={`thread-header-container ${thread._id}`}>
                        <div style={{ display: 'flex' }}>
                            <img className="thread-header-author-avatar" src={thread.author.avatar} />
                            <div className="thread-header-author-name">{thread.author.name}</div>
                            <div className="thread-header-created-at">{humanReadableTimeFrom(thread._id)}</div>
                        </div>
                        <div onClick={this.handleResolve} className="thread-header-resolve-button jam---button">Resolve</div>
                    </div>
                    {thread.comments.map((comment, index) => {
                        let rawMessageBlocks
                        try {
                           rawMessageBlocks = JSON.parse(comment.message)
                        } catch (e) {
                           rawMessageBlocks = false
                        }
                     if (index == 0) {
                         return (
                            <div id={`thread-message-${thread._id}`} key={`thread-message-${thread._id}`} className="thread-message first-comment">
                                <JamEditorDisplayMode rawMessageBlocks={rawMessageBlocks} />
                            </div>
                         )
                     } else {
                         return (
                            <div key={`thread-comment-${index}-${thread._id}`} className={`comment-container ${thread._id}`}>
                                <div className="comment-header">
                                    <img className="comment-avatar" src={comment.author.avatar} />
                                    <div className="comment-author">{comment.author.name}</div>
                                    <div className="comment-time">{humanReadableTimeFrom(comment._id)}</div>
                                </div>
                                <JamEditorDisplayMode rawMessageBlocks={rawMessageBlocks} />
                            </div>
                         )
                     }   
                    })}
                    <JamThreadEditor loggedInUser={loggedInUser} thread={thread} updateThread={updateThread} mentions={this.props.mentions} />
                </div>
                // </Draggable>
            )
        } else {
            return (
                <JamPreview 
                    handlePreviewClick={this.handlePreviewClick} 
                    thread={thread} 
                    updateThread={updateThread} 
                    toggleDragStatus={this.toggleDragStatus} 
                    updateThreadPosition={this.updateThreadPosition}
                    threadX={threadX}
                    threadY={threadY}
                />
            )
            
        }
    }    
}